
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-design-barcode',
  templateUrl: './design-barcode.component.html',
  styleUrls: ['./design-barcode.component.scss']
})
export class DesignBarcodeComponent implements OnInit {

  constructor(
    private router: Router,
  ) { }

  ngOnInit(): void {
  }

  gotoLabelProducts(){
    this.router.navigate(['/cafe/dashboard/management/design/design-label/label-products']);
  }
  gotoLabelLots(){
    this.router.navigate(['/cafe/dashboard/management/design/design-label/label-lots']);
  }
  gotoLabelDish(){
    this.router.navigate(['/cafe/dashboard/management/design/design-label/label-dish']);
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/design']);
  }
}
