import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DesignBarcodeComponent } from './design-barcode.component'; 
import { RouterModule, Routes } from '@angular/router'; 
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import {DragDropModule} from '@angular/cdk/drag-drop'; 
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module'; 
const routes: Routes = [
  {
    path: '',
    component: DesignBarcodeComponent
  },
  {
    path: 'label-products',
    loadChildren: () => import('./label-products/label-products.module').then(m => m.LabelProductsModule)
  },
  {
    path: 'label-lots',
    loadChildren: () => import('./label-lots/label-lots.module').then(m => m.LabelLotsModule)
  },
  {
    path: 'label-dish',
    loadChildren: () => import('./label-dish/label-dish.module').then(m => m.LabelDishModule)
  },
]


@NgModule({
  declarations: [
    DesignBarcodeComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    DragDropModule
  ]
})
export class DesignBarcodeModule { }
